
#index {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  /* color: #2c3e50; */
}
.el-row {
    margin-bottom: 20px;
&:last-child {
      margin-bottom: 0;
}
}
.el-col {
    border-radius: 4px;
}
.bg-purple-dark {
    background: #99a9bf;
}
.bg-purple {
    background: #fff;
}

/* .bg-purple-light {
    background: #e5e9f2;
  } */
.grid-content {
    border-radius: 4px;
    min-height: 36px;
}
.row-bg {
    padding: 10px 0;
    background-color: #f9fafc;
}
.content {
    overflow: hidden;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    /* background-color: #fff; */
    -webkit-box-pack: center;
        -ms-flex-pack: center;
            justify-content: center;
    -webkit-box-align: center;
        -ms-flex-align: center;
            align-items: center;
    padding: 10%;
}
.img-content {
    display: block;
    width: 30%;
    float: left;
}
.el-main {
    line-height: unset;
    text-align: start
}
.num-content {
    width: 70%;
    margin-left: 10%;
    line-height:25px;
    padding-left:5%;
}
.num-content p{text-align:left;}
.order-num {
    font-size: 16px;
    font-weight: 700;
}
.order-count {
    color: #449cff;
    font-size: 2.2em;
}
.unit {
    margin-left: 8px;
}
#myChart {
    width: 100%;
    height: 500px;
    background-color: #fff;
}

